
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&family=Source+Serif+4:wght@200;300;400;500;600;700;800;900&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --primary-color:#E5B528;

}

ul{
  list-style: none;
}

h1,h2{
  font-family:"Source Serif 4",sans-serif;
}

h1{
  font-size:5rem;
}

h2{
  font-size:3rem;
}

h4{
  font-size:2rem;
}

h3{
  font-size:1.4rem;
  text-transform:uppercase;
  color:var(--primary-color);
  margin:.5rem 0;
}

li:hover{
  cursor:pointer;
}

.btn{
  padding:.7rem 1.5rem;
  color:#000;
  background:transparent;
  border:1px solid #000;
  font-size:1.4rem;
  border-radius:25px;
}

.btn-dark{
  background-color: #000;
  color:var(--primary-color);
}

.btn:hover{
  cursor: pointer;
}

@media screen and(max-width:740px){
  h1{
    font-size:3rem;
  }
  h2{
    font-size:2rem;
  }
}